import React from 'react'

const Contador = ({inicial}) => {

    const [contador, setContador] = React.useState(inicial);

    const aumentar = () => setContador(contador + inicial);

    const disminuir = () => setContador(contador - inicial);

    return (
        <div>
            <h1 className={contador > 0 ? "mayor" : contador < 0 ? "menor" : ""}>Contador: {contador}</h1>
            <hr/>
            <button class="redButton" onClick={disminuir}>Disminuir</button>
            <button class="greenButton" onClick={aumentar}>Aumentar</button>
        </div>
    )
}

export default Contador;
